@import url(https://fonts.googleapis.com/css?family=Neucha);
.card {
  background-color: white;
  box-shadow: 0px 8px 20px -9px rgba(0, 0, 0, 0.55);
  text-align: center;
  align-content: center;
  border-radius: 6px;
  float: left;
  overflow-wrap: break-word;
  border: 1px solid #ccc;
  margin: 20px;
  padding: 20px;
  display: flex;
  align-content: start;
}

.card figure {
  width: 100%;
  margin: 0px;
  padding: 0px;
}

.card .tags {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #333;
}

.card .tags {
  text-align: left;
}

@media (max-width: 40.063em) {
  .card {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    clear: both;
  }
}

@media (min-width: 40.063em) and (max-width: 64em) {
  .card {
    width: 320px;
  }
}

@media (min-width: 64.063em) {
  .card {
    width: 290px;
  }
}

* {
  box-sizing: border-box;
}

.flexWrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.App {
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.header {
  background-color: #222;
  color: white;
  width: 100%;
  clear: both;
  margin: 0;
}

.header h2 {
  font-family: Neucha, cursive;
  padding-top: 8px;
}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    
@media (max-width: 40.063em) {
  .App {
    width: 100%;
    display: block;
    box-sizing: border-box;
  }
}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   

@media (min-width: 40.063em) and (max-width: 64em) {
  .App {
    width: 720px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 64.063em) {
  .App {
    width: 96vw;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
}

