@import url(https://fonts.googleapis.com/css?family=Neucha);
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.flexWrap {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: space-between;
}

.App {
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.header {
  background-color: #222;
  color: white;
  width: 100%;
  clear: both;
  margin: 0;
}

.header h2 {
  font-family: Neucha, cursive;
  padding-top: 8px;
}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    
@media (max-width: 40.063em) {
  .App {
    width: 100%;
    display: block;
    box-sizing: border-box;
  }
}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   

@media (min-width: 40.063em) and (max-width: 64em) {
  .App {
    width: 720px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 64.063em) {
  .App {
    width: 96vw;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
}
